.container-list {

    max-height: 610px;
    overflow: auto;

    .content-list {

        display: grid;
        grid-template-columns: var(--checkWidth) calc((var(--inputWidth) - 3.8rem));
        -webkit-column-gap: 1px;
                column-gap: 1px;
        margin: auto;
        margin-bottom: 1px;  
        border-radius: 3px;

        .btn-checkbox {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            -ms-flex-pack: center;
                justify-content: center;
            background-color: #f54d4d;
            width: calc(var(--checkWidth));
            height: var(--checkHeigth);
            border-radius: inherit;
        }

        .added-task {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            background-color: #51a068;
            width: calc((var(--inputWidth) - 3.8rem - 25px));
            height: var(--checkHeigth);
            border-radius: inherit;
            padding-left: 25px;

            &:hover {
                color: white;
            }
        }
    }
}


